:local(.link) {
  text-decoration: none;
  color: #222;
}

:local(.list:hover) {
  background-color: aquamarine;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}

:local(.list) {
  padding: 10px;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  margin-top: 10px;
}

:local(.info) {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

:local(.info > h6) {
  -ms-flex: 1 1;
      flex: 1 1;
}

:local(.tags) {
  display: -ms-flexbox;
  display: flex;
}

:local(.tags > div) {
  margin-right: 5px;
}



